import React, { useRef, useEffect } from 'react';
import $ from 'jquery';
import Position from '../components/Position';
import { scrollType } from '../App';


function PositionContainer ({scrollTarget, scrollTop}: scrollType) {
    const sectionArrayHeight = useRef<number[]>([]);

    // 각 section 높이 구하기
    useEffect(() => {
        const target = scrollTarget.current;
        const sections = target?.querySelectorAll('.contents > section');
        if(!sections) { return};
        const heights = Array.from(sections).map(v => v.clientHeight);
        sectionArrayHeight.current = heights;
    }, []);


    const onClick = (index: number) => (e:React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        const target = scrollTarget.current;

        if(!target) { throw new Error('Undefined props') }
        if(!sectionArrayHeight) { throw new Error('Undefined ref') }
        let totHeight = 0;
        for(let i=0; i<index-1; i++) {
            totHeight += sectionArrayHeight.current[i]
        }
        /**
         * TODO: DOM 조작 안하고 jquery 안 쓰는 방법 찾기
         */
        $(target).animate({
            scrollTop: totHeight
        }, 800);
    }

    const setActiveState = () => {
        const heights = sectionArrayHeight.current;
        const calcHeights:number[] = [];
        heights.reduce((a,c) => {
            calcHeights.push(a+c);
            return a+c
        },0);

        if(calcHeights.length === 0) { return 0; }
        let i=0;
        for(; i<calcHeights.length; i++) {
            if(scrollTop < calcHeights[i]) {
                break;
            }
        }
        return i<calcHeights.length ? i : calcHeights.length-1;
    }

    return (
        <Position onClick={onClick} setActiveState={setActiveState()}/>
    )
}
export default PositionContainer;