import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPhoneAlt, faEnvelope, faHome  } from '@fortawesome/free-solid-svg-icons';
import { faGithub } from '@fortawesome/free-brands-svg-icons';

function contact () {
    return (
        <section className='contact'>
            <div className='container'>
                <p>Contact</p>
                <div className='phone'><FontAwesomeIcon icon={faPhoneAlt} /> 010.5376.9883</div>
                <div className='email'><FontAwesomeIcon icon={faEnvelope} /> gipark1993@gmail.com</div>
                <div className="github"><FontAwesomeIcon icon={faGithub} /> https://github.com/GihongPark </div>
                <div className="site"><FontAwesomeIcon icon={faHome} /> https://gihongpark.github.io/</div>
            </div>
        </section>
    )
}
export default contact;