import React from 'react';

function skill () {
    return (
        <section className='skill'>
            <p>Skill</p>
            <div>
                <img className='logo' src={require('../../assets/img/HTML5_logo.png')} alt='HTML5' />
                <img className='logo' src={require('../../assets/img/CSS3_logo.png')} alt='CSS3' />
                <img className='logo' src={require('../../assets/img/JS_logo.png')} alt='Javascript' />
                <img className='logo' src={require('../../assets/img/TS_logo.png')} alt='Typescript' />
                <img className='logo' src={require('../../assets/img/React_logo.png')} alt='React' />
                <img className='logo' src={require('../../assets/img/SASS_logo.png')} alt='Sass' />
            </div>
        </section>
    )
}
export default skill;