import React from 'react';
import Content from './content';
import './portfolio.scss';
import portInfo from './portfolio.json';

function portfolio () {
    return (
        <section className='portfolio'>
            <h2><span>Portfolio</span></h2>
            <Content direction='left' portInfo={portInfo[0]} />
            <Content direction='right' portInfo={portInfo[1]} />
            <Content direction='left' portInfo={portInfo[2]} />
            <Content direction='right' portInfo={portInfo[3]} />
        </section>
    )
}

export default React.memo(portfolio);