import React, { useRef, useEffect } from 'react';
import './position.scss';

type PositionType = {
    onClick : (i: number) => (e:React.MouseEvent<HTMLButtonElement, MouseEvent>) => void
    setActiveState : number
}
function Position ({onClick, setActiveState}: PositionType) {
    const list = useRef<HTMLUListElement>(null);
    
    useEffect(() => {
        list.current?.querySelector('.active')?.classList.remove('active');

        const i = setActiveState;
        const li = list.current?.querySelectorAll('li')[i];
        if(!li) { throw new Error('Undifiend element') }
        li.className='active';
    },[setActiveState])

    // const setActiveClass = () => {
        
    // }
    
    // setActiveClass();
    return (
        <nav className='position'>
            <ul ref={list}>
                <li><button onClick={onClick(1)}><em>01</em><span>HOME</span></button></li>
                <li><button onClick={onClick(2)}><em>02</em><span>Profile</span></button></li>
                <li><button onClick={onClick(3)}><em>03</em><span>Portfolio</span></button></li>
                {/* <li><button onClick={onClick(4)}><em>04</em><span>Contact</span></button></li> */}
            </ul>
        </nav>
    )
}

export default Position;