import React from 'react';
import AboutMe from './aboutMe';
import SKill from './skill';
import Contact from './contact';
import './profile.scss';

function Profile () {

    return (
        <section className='profile'>
            <h2><span>Profile</span></h2>
            <div className='row'>
                <AboutMe />
                <Contact />
            </div>
            <SKill />
        </section>
    )
}

export default React.memo(Profile);