import React from 'react';
import ScrollAnimation from 'react-animate-on-scroll';

type contentType = {
    direction: string,
    portInfo: any,
}
function content ({direction, portInfo}:contentType) {
    if(direction === 'left') {
        return (
            <div className='project left'>
                {img('Left',portInfo.src)}
                {desc(portInfo)}
            </div>
        )
    } else {
        return (
            <div className='project right'>
                {desc(portInfo)}
                {img('Right',portInfo.src)}
            </div>
        )
    }
}
function img (direction:string, source:string) {
    const animateIn = `bounceIn${direction}`;

    return (
        <ScrollAnimation className='img'
            scrollableParentSelector='.App'
            animateIn={animateIn}
            animateOnce={true}
            duration={2}
        >
            <img src={require(`../../assets/img/${source}.png`)} alt={source}/>
        </ScrollAnimation>
    )
}
function desc (desc: any) {
    let URL;
    if (desc.no === 1) { 
        URL = desc.url;
    } else {  // 동영상, PDF 파일 가져오기
        URL = require(`../../assets/${desc.url}`);
    }
    return (
        <ScrollAnimation className='desc'
            scrollableParentSelector='.App'
            animateIn='fadeIn'
            animateOnce={true}
            duration={2}
            delay={800}
        >
           <p className='desc_no'>PROJECT 0{desc.no}</p>
           <h3 className='desc_title'>{desc.title}</h3>
           <a href={URL} target='_blank'><em>VIEW</em></a>
        </ScrollAnimation>
    )
}

export default content;