import React, { useRef, useState } from 'react';
import './components/style/Reset.scss';
import './App.scss';

import PositionContainer from './containers/PositionContainer';
import Home from './components/Home';
import Profile from './components/Profile';
import Portfolio from './components/Portfolio';

export type scrollType = {
  scrollTarget: React.RefObject<HTMLDivElement>,
  scrollTop: number
}
function App() {
  const [scrollTop, setScrollTop] = useState<number>(0);
  const scrollTarget = useRef<HTMLDivElement>(null);

  const onScroll = (e: React.UIEvent<HTMLDivElement, UIEvent>) => {
    setScrollTop(e.currentTarget.scrollTop);
  }

  return (
    <div className="App" onScroll={onScroll} ref={scrollTarget}>
      <PositionContainer scrollTarget={scrollTarget} scrollTop={scrollTop} />
      <div className='menu'></div>
      <section className='contents'>
        <Home />
        <Profile />
        <Portfolio />
      </section>
    </div>
  );
}

export default App;
